.modal-box {
    min-height: 100vh;
    background-color: #4070f4;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}
.wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 345px;
    width: 100%;
    background: #fff;
    border-radius: 8px;
    padding: 15px 25px 22px;
    transition: right 0.3s ease;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}
.wrapper.show {
    right: 20px;
}
.wrapper header {
    align-items: center;
    column-gap: 15px;
}
header i {
    color: #d7984b;
    font-size: 55px;
    top: 50%;
    position: relative;
    left: 50%;
    transform: translate(-50%, 10px);
}
header h2 {
    color: #333;
    font-weight: 500;
    font-size: 25px;
    text-align: center;
}
.wrapper .data {
    margin-top: 16px;
}
.wrapper .data p {
    color: #7d7d7d;
    font-size: 16px;
    text-align: center;
}
.data a {
    color: #daa768;
    text-decoration: none;
    margin-top: 20px;
    position: relative;
    left: 35%;
}
.data p a:hover {
    text-decoration: underline;
}

.CookieConsent {
    background: none !important;
    justify-content: center !important;
    position: relative !important;
}

#rcc-confirm-button {
    border: none !important;
    color: #fff !important;
    padding: 14px 30px !important;
    border-radius: 4px !important;
    background: #d7984b !important;
    cursor: pointer !important;
    transition: all 0.2s ease !important;
}

.CookieConsent {
    position: absolute !important;
    width: 45% !important;
    top: 50% !important;
    left: 50% !important;
    transform: translate(-50%, -50%) !important;
    background-color: white !important;
    display: block !important;
    padding: 40px !important;
}

.CookieConsent .cookies_area .bottom_text {
    text-align: center;
}

.CookieConsent .cookies_area .bottom_text h5 {
    font-size: 22px;
    font-weight: 600;
    text-align: center;
    color: black;
    margin-bottom: 20px;
}

.CookieConsent .cookies_area .bottom_text p {
    font-size: 17px;
    font-weight: 300;
    color: black;
    margin-bottom: 20px;
}

.CookieConsent .cookies_area .bottom_text h4 {
    font-size: 18px;
    font-weight: 400;
    color: #d7984b;
}

.CookieConsent .btnclass {
    text-align: center;
}